<template>
    <examples-demo title=" input" desc="" :code="code.formInput"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-input slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormInput from "./example-page-template-form-input";

  export default {
    name: "example-page-template-form-input-demo",
    components: {ExamplePageTemplateFormInput, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns:[
            {
              title: '',
              key: 'prop'
            },
            {
              title: '',
              key: 'desc'
            },
            {
              title: '',
              key: 'type'
            },
            {
              title: '',
              key: 'default'
            }
          ],
        data:[
          {
            prop: 'fullModel',
            desc: ',',
            type: '*',
            default: ''
          },
          {
            prop: 'model',
            desc: 'fullModel,modelmodel,' +
            'fullModel,$rootfullModel,model,' +
            '"a.b.c",fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop:'prop',
            desc:'v-model',
            type:'String',
            default:''
          },
          {
            prop:'rules',
            desc:'',
            type:'Object|Array',
            default:''
          },
          {
            prop:'extend:type',
            desc:' textpasswordtextareaurlemaildate',
            type:'String',
            default:''
          },
          {
            prop:'extend:size',
            desc:'largesmalldefault',
            type:'String',
            default:''
          },
          {
            prop:'extend:placeholder',
            desc:'',
            type:'String',
            default:''
          },
          {
            prop:'extend:clearable',
            desc:'',
            type:'String',
            default:''
          },
          {
            prop:'extend:disabled',
            desc:'',
            type:'String',
            default:''
          },
          {
            prop:'extend:readonly',
            desc:'',
            type:'String',
            default:''
          },
          {
            prop:'extend:maxlength',
            desc:'',
            type:'String',
            default:''
          }
        ]

      }
    }
  }
</script>



