<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
  /**
   *@desc     
   *@author   
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-select",
    data() {
      return {
        pageData: {
          //
          page: {
            title: ''
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //
                    model: {
                      normalSelect1:'female',
                      normalSelect2:'female',
                      normalSelect3:'female',
                    },
                    //
                    rules: {
                    },
                    //Form
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'select',
                          label: ' type:select',
                          prop: 'normalSelect1',
                          extend:{
                            placeholder:'placeholder',
                            span:12
                          },
                          items: [
                            {
                              label: '',
                              value: 'male',
                              extend:{
                              }
                            },
                            {
                              label: '',
                              value: 'female'
                            }
                          ],
                        },
                        {
                          type: 'select',
                          label: ' type:select',
                          prop: 'normalSelect2',
                          excat:[{
                            type:'lookup-map'
                          }],
                          extend:{
                            placeholder:'placeholder',
                            span:12
                          },
                          items: [
                            {
                              label: '',
                              value: 'male',
                              extend:{
                              }
                            },
                            {
                              label: '',
                              value: 'female'
                            }
                          ],
                        },
                        {
                          type: 'select',
                          label: ' type:select',
                          prop: 'normalSelect3',
                          excat:[{
                            type:'lookup-fee'
                          }],
                          extend:{
                            placeholder:'placeholder',
                            span:12
                          },
                          items: [
                            {
                              label: '',
                              value: 'male',
                              extend:{
                              }
                            },
                            {
                              label: '',
                              value: 'female'
                            }
                          ],
                        },
                        {
                          type: "select",
                          label: "",
                          prop: 'normalSelect3',
                          dynamic:{
                            url:"/post/qryMailInfoDetail",//
                            params:{ //
                              aa:11,
                              bb:22
                            },
                            label:"title", //labelvalue
                            value:"val"
                          },
                          extend: {
                            eventType: "advTipUpdate",
                            placeholder: "",
                            itemWidth: 320
                          }
                        }
                      ]
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



