<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
    /**
     *@author       
     *@date         2018/12/15 21:54
     *@Copyright    
     *@Description
     */
    export default {
        name: "example-multiple-selection",
        data() {
        return {
          pageData: {
            //
            page: {
              title: 'mail-licence',
              fullModel: {
                supplementType:"",
                FWQT:"",
                other:[],
                "bothItem":""
              }
            },
            //
            elements: [
              {
                "type":"box",
                "data":[ {
                  "type": "form",
                  "data": {
                    "extend": {
                      "span": 17,
                      "labelWidth":250
                    },
                    "items": [
                      {
                        "type": "checkboxMultiple",
                        "label": "",
                        "prop": "other",
                        "extend":{
                          "otherBtn":"FWQT",
                          "bind":"supplementType",
                          "mockData":[
                            {"label":"","value":"0"},
                            {"label":"","value":"1"}
                          ]
                        }
                      },
                      {
                        "type": "input",
                        "label": "",
                        "prop": "bothItem",
                        "extend": {
                          "placeholder": "",
                          "itemWidth":350
                        },
                        "excat":[
                          {
                            "type":"input-note",
                            "text":"100%100%20%20%"
                          }
                        ]
                      }
                    ]
                  }
                }]
              }
            ]
          }
        }
      }
    }
</script>

<style scoped>

</style>



