<template>
    <examples-demo title=" label" desc="" :code="code.formLabel"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-label slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormLabel from "./example-page-template-form-label";

  export default {
    name: "example-page-template-all-demo",
    components: {ExamplePageTemplateFormLabel, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '',
            key: 'prop'
          },
          {
            title: '',
            key: 'desc'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'fullModel',
            desc: ',',
            type: '*',
            default: ''
          },
          {
            prop: 'model',
            desc: 'fullModel,modelmodel,' +
            'fullModel,$rootfullModel,model,' +
            '"a.b.c",fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop: 'extend',
            desc: 'formformItem',
            type: 'Object',
            default: ''
          },
          {
            prop: 'form:labelWidth',
            desc: 'formlabel',
            type: 'Number',
            default: ''
          },
          {
            prop: 'form:span',
            desc: 'formvalue',
            type: 'Number',
            default: ''
          },
          {
            prop: 'items',
            desc: 'form-item',
            type: 'Array',
            default: ''
          },
          {
            prop: 'type',
            desc: '',
            type: 'String',
            default: ''
          },
          {
            prop: 'prop',
            desc: 'v-model',
            type: 'String',
            default: ''
          },
          {
            prop: 'extend:linkageChangeValue',
            desc: '',
            type: 'Array',
            default: ''
          },
          {
            prop: 'extend:linkageChangeValue:type',
            desc: 'type=out   type=into ',
            type: 'String',
            default: 'out'
          },
          {
            prop: 'extend:linkageChangeValue:changeKey',
            desc: '',
            type: 'String',
            default: ''
          },
          {
            prop: 'extend:linkageChangeValue:noChangeExist',
            desc: 'changeKey',
            type: 'Boolean',
            default: ''
          },
          {
            prop: 'extend:linkageChangeValue:values',
            desc: 'changeKeychangeParam',
            type: 'Array',
            default: ''
          }
        ]
      }
    }
  }
</script>



