<template>
  <examples-demo title=" info-attachment" desc="" :code="code.infoAttachment"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-info-attachment slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       
     *@date         2018/9/10 14:21
     *@Copyright    
     *@Description  
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplateInfoAttachment from "./example-page-template-info-attachment";
    export default {
        name: "example-page-template-info-attachment-demo",
        components: {ExamplePageTemplateInfoAttachment, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '',
                key: 'prop'
              },
              {
                title: '',
                key: 'desc'
              },
              {
                title: '',
                key: 'type'
              },
              {
                title: '',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: ',',
                type: '*',
                default: ''
              },
              {
                prop: 'model',
                desc: 'fullModel,modelmodel,' +
                'fullModel,$rootfullModel,model,' +
                '"a.b.c",fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop: 'headerTitle',
                desc: '',
                type: 'String',
                default: ''
              },
              {
                prop: 'showHeader',
                desc: ',',
                type: 'Boolean',
                default: 'false'
              }
            ]
          }
        }
    }
</script>

<style scoped>

</style>



