<template>
    <examples-demo title=" step" desc="" :code="code.step"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-step slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateStep from "./example-page-template-step";

  export default {
    name: "example-page-template-all-demo",
    components: {ExamplePageTemplateStep, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '',
            key: 'prop'
          },
          {
            title: '',
            key: 'desc'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'current',
            desc: ',',
            type: 'Number',
            default: ''
          },
          {
            prop: 'steps',
            desc: '',
            type: 'Array',
            default: ''
          },
          {
            prop: 'title',
            desc: '',
            type: 'String',
            default: ''
          }
        ]
      }
    }
  }
</script>



