<template>
  <div class="form-org-info">
    <Row type="flex" :justify="extend.justify||'center'">
      <Col :span="extend.span||12">
        <Form  class="ty-content-element-form" v-model="realModel"  :label-width="extend.labelWidth||205">
          <FormItem label=":">
            <Label v-if="realModel.applyName">{{realModel.applyName}}</Label>
            <Input v-else v-model="realModel.applyName"></Input>
          </FormItem>
          <FormItem label=":">
            <Label v-if="realModel.legalTypeDesc">{{realModel.qualificationDesc}}</Label>
            <Select v-else v-model="realModel.qualification">
              <Option v-for="item in legalTypes" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem label=":">
            <Label v-if="realModel.qualificationDesc">{{realModel.qualificationDesc}}</Label>
            <Select v-else v-model="realModel.qualification">
              <Option v-for="item in qualifications" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem label=":">
            <Label v-if="realModel.address">{{realModel.address}}</Label>
            <Input v-else v-model="realModel.address"></Input>
          </FormItem>
          <FormItem label=":">
            <Label v-if="realModel.legalMan">{{realModel.legalMan}}</Label>
            <Input v-else v-model="realModel.legalMan"></Input>
          </FormItem>
        </Form>
      </Col>
    </Row>
  </div>
</template>

<script>
  /**
   *@author       
   *@date         2018/12/15 17:30
   *@Copyright    
   *@Description
   */
  import {realModel} from "../mixin-page-template";

  export default {
    mixins: [realModel],
    name: "ty-form-org-info",
    props: {
      extend: {
        default() {
          return {};
        }
      },
    },
    data(){
      return {
        //
        qualifications:[

        ],
        //
        legalTypes:[
          {
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: ''
          },
          {
            value: '3',
            label: ''
          },
          {
            value: '4',
            label: ''
          },
          {
            value: '5',
            label: ''
          },
          {
            value: '6',
            label: ''
          },
          {
            value: '7',
            label: ''
          },
          {
            value: '8',
            label: ''
          },
          {
            value: '9',
            label: ''
          }
        ]
      }
    }
  }
</script>

<style scoped>

</style>



