let Code = {};
Code.base=`
<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  /**
   *@desc
   *@author  
   *@date  2018.12.15
   */
  export default {
    name:"example-essential-info",
    data() {
      return {
        pageData: {
          //
          page: {
            title: 'select',
            fullModel: {
              "askNum":"",
              "rasonChange":"",
              "makeCode":"",
              "sellName":"",
              "sellCardType":"",
              "sellCardNumber":"",
              "sellAddress":"",
              "sellPhone":"",
              "buyName":""

            }
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  "type": "essential-info",
                  "data":{
                    "infoData":[
                      {
                        "title": "",
                        "data": [
                          {
                            "title": "",
                            "prop": "askNum"
                          },
                          {
                            "title": "",
                            "prop": "rasonChange"
                          },
                          {
                            "title": "",
                            "prop": "makeCode"
                          }
                        ]
                      }
                    ]
                  }
                },
                {
                  "type": "essential-info",
                  "data":{
                    "infoData":[
                      {
                        "title": "",
                        "data": [
                          {
                            "title": "",
                            "prop": "sellName"
                          },
                          {
                            "title": "",
                            "prop": "sellCardType"
                          },
                          {
                            "title": "",
                            "prop": "sellCardNumber"
                          },
                          {
                            "title": "",
                            "prop": "sellAddress"
                          },
                          {
                            "title": "",
                            "prop": "sellPhone"
                          },
                          {
                            "title": "",
                            "prop": "buyName"
                          },
                          {
                            "title": "",
                            "prop": "buyCardType"
                          },
                          {
                            "title": "",
                            "prop": "buyCardNumber"
                          }
                        ]
                      }
                    ]
                  }
                }
              ]
            }
          ]
        },
      }
    },
    methods: {
    }
  }
</script>

`;
export default Code;



