<template>
  <div class="ty-content-element-lookup">
    <div class="ty-content-element-lookup-fee">
      <a class="charge-standard" @click="open"></a>
      <Modal :scrollable="true"
             v-model="modal" width="750">
        <div style="min-height: 100px;padding:12px 0; margin: 0">
          <div class="content-model1"><span class="content-title"></span></div>
          <div class="content-model"><span class="content-title"></span></div>
          <div class="content-model"><span class="content-title"></span>[2006]69</div>
          <IvTable ref="table" v-bind="table"/>
        </div>
      </Modal>
    </div>
  </div>
</template>

<script>
  import IvTable from "../../../iv-table";

  /**
   *@author       
   *@date         2018/9/7 01:00
   *@Copyright    
   *@Description  
   */
  export default {
    name: "ty-content-element-lookup-fee",
    components: {IvTable},
    data() {
      return {
        modal: false,
        table: {
          // url: 'test',
          data: [
            {inquiryCode: "()", professionalOrgName: "/", purchaseCategoryName: 110},
            {inquiryCode: "", professionalOrgName: "/", purchaseCategoryName: 7000},
          ],
          // pageNo: 1,
          autoLoad: false,
          singleSelect: true,
          // height: 400,
          // queryParam: function (param) {
          //   console.log('queryParam:', param)
          //   return _.assign({test: 1}, param);
          // },
          // responseHandler: function (val) {
          //   console.log('responseHandler:', val)
          //   return val;
          // },
          columns: [
            {
              title: '',
              key: 'inquiryCode',
              align: 'left',
            },
            {
              title: '',
              key: 'professionalOrgName',
              align: 'left',
              width: 140
            },
            {
              title: '',
              key: 'purchaseCategoryName',
              align: 'right',
              width: 180,
              render: (h, params) => {
                return h('span', (params.row.purchaseCategoryName).toFixed(2));
              }
            },
          ]
        }
      }
    },
    methods: {
      //note 
      open() {
        this.modal = true
      },
    }
  }
</script>
<style lang="less">
  .content-model1 {
    .content-title {
      color: #333;
    }
  }

  .content-model {
    display: inline-block;
    width: 355px;
    .content-title {
      color: #333;
    }
  }
</style>



