<template>
  <div>
    <Button @click="click"></Button>
    <Input v-model="casedata.value[2]"/>
    <Cascader ref="cascader" :data="casedata.data" v-model="casedata.value" @on-change="onChange"></Cascader>
  </div>
</template>
<script>
  export default {
    model: {
      prop: 'casedata',
      event: 'change'
    },
    props: {
      casedata: {
        type: Object,
        default() {
          return {
            data: [],
            value: []
          };
        }
      }
    },
    watch: {
      casedata() {
        this.$nextTick(() => {
          this.$refs.cascader.updateSelected()
        })
      }
    },
    methods: {
      click() {
        this.$emit(
          'change', {
            value: ['jiangsu', 'suzhou', 'shizilin'],
            data: [
              {
                value: 'beijing',
                label: '',
                children: [
                  {
                    value: 'gugong',
                    label: ''
                  },
                  {
                    value: 'tiantan',
                    label: ''
                  },
                  {
                    value: 'wangfujing',
                    label: ''
                  }
                ]
              }, {
                value: 'jiangsu',
                label: '',
                children: [
                  {
                    value: 'nanjing',
                    label: '',
                    children: [
                      {
                        value: 'fuzimiao',
                        label: '',
                      }
                    ]
                  },
                  {
                    value: 'suzhou',
                    label: '',
                    children: [
                      {
                        value: 'zhuozhengyuan',
                        label: '',
                      },
                      {
                        value: 'shizilin',
                        label: '',
                      }
                    ]
                  }
                ],
              }]
          }
        );
      },
      onChange(value) {
        this.$emit('change', {
          data: this.casedata.data,
          value: value,
        })
      }
    }
  }
</script>



