<template>
  <div class="timeLine" v-bind="realModel">
    <Card style="text-align: center">
      <!---->
      <div v-if="realState==='0'||realState==='23'||realState==='34'" class="notPass">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-close-circle" color="#F85555" size="52"/>
          <p class="p1"></p>
        </div>
      </div>
      <!---->
      <div
        v-else-if="realState==='1'||realState==='3'||realState==='330'|| realState==='320'||realState==='311'||realState==='390'||realState==='331'||realState==='321'||realState==='204'||realState==='205'||realState==='206'||realState==='207'"
        class="examine">
        <span class="code">:{{fullModel.projId}}</span>
        <p class="p1"></p>
        <div class="notice">
          <p class="p2">
            <template v-if="fullModel.deal_type=='02'">
              
            </template>
            <template v-else>
              <template v-if="fullModel.promiseComptime==''">
                
              </template>
              <template v-else>
                {{fullModel.promiseComptime}}
              </template>
            </template>
          </p>
          <p class="p2"></p>
        </div>
        <div class="time">
          <Icon type="md-alarm" size="20" color="orange"/>
          <span>&nbsp;<template v-if="fullModel.days!==undefined">
                {{fullModel.days}}
              </template><template v-else>
                1
              </template>&nbsp;</span>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='4'" class="complete">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-checkmark-circle" color="#2BA738" size="52"/>
          <p class="p1"></p>
        </div>
        <!--<div class="pay">-->
          <!--<Button type="warning" ghost @click="pay"></Button>-->
        <!--</div>-->
      </div>
      <!---->
      <div v-else-if="realState==='480'" class="complete">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-checkmark-circle"color="#2BA738" size="52" />
          <p class="p1"></p>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='501'||realState==='6'||realState==='5'||realState==='98'||realState==='99'" class="handle">
        <span class="code">:{{fullModel.projId}}</span>
        <p class="title">  </p>
        <div class="notice">
          <p></p>
        </div>
        <div class="btn" v-if="fullModel.fileUrlZz">
          <Button class="bt_down_load" type="error" ghost @click="download" style="border-radius:43px">
            
          </Button>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='450'" class="handle">
        <span class="code">:{{fullModel.projId}}</span>
        <p class="title">  </p>
        <div class="notice">
          <p></p>
        </div>
        <div class="btn" v-if="fullModel.fileUrlZz">
          <Button class="bt_down_load" type="error" ghost @click="download" style="border-radius:43px">
            
          </Button>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='210' ||realState==='310'||realState==='202'" class="supplement">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice" color="#F93">
          <Icon type="ios-alert" size="52"/>
          <p class="p1"></p>
        </div>
        <!--<div class="perfect">-->
          <!--<Button type="warning" ghost @click="perfect"></Button>-->
        <!--</div>-->
      </div>
      <!---->
      <div v-else-if="realState==='8002'" class="houseFeedback">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1"></p>
          <p class="p2"></p>
        </div>
        <div class="time">
          {{fullModel.askNum}}
        </div>
      </div>
      <div v-else-if="realState==='8003'" class="houseConfirmed">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1"></p>
          <p class="p2">{{fullModel.days}}</p>
        </div>
        <div class="time">
          {{fullModel.askNum}}
        </div>
        <!--<div class="perfect">-->
          <!--<Button type="warning" ghost @click="sure"></Button>-->
        <!--</div>-->
      </div>
      <!---->
      <div v-else-if="realState==='8102'" class="houseFeedback">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1"></p>
          <p class="p2">{{fullModel.days}}</p>
        </div>
        <div class="time">
          {{fullModel.askNum}}
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='201'||realState==='203' " class="ConfirmContract">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1"></p>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='2'||realState==='211'" class="ConfirmContract">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1"></p>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='24'" class="ConfirmContract">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1"></p>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='invalid'" class="notPass">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-close-circle" color="#F85555" size="52"/>
          <p class="p1"></p>
        </div>
      </div>
      <!---->
      <div v-else-if="realState==='valid'" class="handle">
        <span class="code">:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-checkmark-circle" color="#2BA738" size="52"/>
          <p class="title"></p>
        </div>
      </div>
      <!---->
      <div class="empty" v-else>
        <span class="code">:{{fullModel.projId}}</span>
      </div>
    </Card>
    <Card v-if="realState!='invalid'" style="text-align: center">
      <div slot="title" style="text-align: left">
        <img src="../assets/img/u49.svg" style="width:2rem;height:2rem;">
        <span style="line-height: 2;font-size:1rem;color:#E84709;">{{fullModel.serviceName}}</span>
      </div>
      <Timeline :pending="pending" style="text-align:left;word-wrap:break-word;word-break:break-all;">
        <TimelineItem v-for="(item,index) in realModel " :key="index" :color="item.color">
          <!--<p style="width:auto" v-html="item.dealTimeStr+'  '+item.operateType+', '+item.content"></p>-->
          <p style="width:auto" v-html="item.dealTimeStr+' '+item.content"></p>
        </TimelineItem>
      </Timeline>
    </Card>
    <Card v-if="realState!='invalid'" style="text-align: center">
      <div v-if="qrCodeShow===true">
        <img :src="codeUri">
        <p> </p>
      </div>
    </Card>
  </div>
</template>
<script>
  import config from '../utils/config'
  import {realModel} from './content-box/mixin-page-template';

  export default {
    mixins: [realModel],
    name: "ty-time-line-h5",
    props: {
      data: {
        type: Object,
        default() {
          return {}
        }
      },
      step: {
        type: String
      },
      qrCodeShow: {
        type: Boolean
      },
      mailShow: {
        type: Boolean,
        default: false
      },
      pending: {
        type: Boolean,
        default: false
      },
      //note ,$root,fullModel,,
      model: {
        type: [String, Object],
        default: '$root',
      },
    },
    data() {
      return {
        flags: true,
      }
    },
    computed: {
      //note state
      realState() {
        return this.fullModel.state;
      },
      //note 
      codeUri() {
        return config.qrCode(this.fullModel.projId)
      },
      //note 
      applyTime() {
        if (this.realModel.length > 0) {
          return this.realModel[0].dealTimeStr
        } else {
          return ''
        }
      },
      //note 
      handleTime() {
        if (this.realModel.length > 0) {
          return this.realModel[this.realModel.length - 1].dealTimeStr
        } else {
          return ''
        }
      }
    },
    methods: {
      //note 
      perfect() {
        let url = window.location.href.split('?')[0] + '?' + `projId=${this.$route.query.projId}&step=${2}&serviceCode=${this.$route.query.serviceCode}&name=${this.$route.query.name}`;
        util.navigate({href:url});
      },
      //note 
      download(){
        const url=this.fullModel.fileUrlZz;
        if (url instanceof Array) {
          for (let item of url) {
            window.open(item.fileUrl)
          }
        }
      },
    },
    created() {
    }
  }
</script>
<style lang="less" scoped>
  .ivu-card {
    margin: 20px 0;
  }
</style>



