<template>
  <examples-demo title=" pap-download-scan" desc="" :code="code.papDownloadScan"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-pap-download-scan slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       
     *@date         2018/9/10 14:40
     *@Copyright    
     *@Description  
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplatePapDownloadScan from "./example-page-template-pap-download-scan";
    export default {
        name: "example-page-template-pap-download-scan-demo",
        components: {ExamplePageTemplatePapDownloadScan, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '',
                key: 'prop'
              },
              {
                title: '',
                key: 'desc'
              },
              {
                title: '',
                key: 'type'
              },
              {
                title: '',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: ',',
                type: '*',
                default: ''
              },
              {
                prop: 'model',
                desc: 'fullModel,modelmodel,' +
                'fullModel,$rootfullModel,model,' +
                '"a.b.c",fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop: 'title',
                desc: ',',
                type: 'String',
                default: ''
              },
              {
                prop: 'btnName',
                desc: ',',
                type: 'String',
                default: ''
              },
            ]
          }
        }
        }
</script>

<style scoped>

</style>



