<template>
  <examples-demo title=" tip-adv" desc="" :code="code.tipAdv"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-tip-adv slot="demo"/>
  </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateTipAdv from "./example-page-template-tip-adv";

  export default {
    name: "example-page-template-tip-adv-demo",
    components: {ExamplePageTemplateTipAdv, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '',
            key: 'prop'
          },
          {
            title: '',
            key: 'desc'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'fullModel',
            desc: ',',
            type: '*',
            default: ''
          },
          {
            prop: 'model',
            desc: 'fullModel,modelmodel,' +
            'fullModel,$rootfullModel,model,' +
            '"a.b.c",fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop: 'eventType',
            desc: 'bus,,,',
            type: 'String',
            default: ''
          },
          {
            prop: 'title',
            desc: ',',
            type: 'String',
            default: ''
          },
          {
            prop: 'feeType',
            desc: ',',
            type: 'String',
            default: ''
          },
          {
            prop: 'feeScale',
            desc: ',',
            type: 'String',
            default: ''
          },
          {
            prop: 'feeUnit',
            desc: ',',
            type: 'String',
            default: ''
          },
          {
            prop: 'fee',
            desc: ',',
            type: 'String',
            default: ''
          },
        ]
      }
    }
  }
</script>



