<template>
  <examples-demo title=" timeLine" desc="" :code="code.timeLine"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-time-line slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       
     *@date         2018/9/10 10:18
     *@Copyright    
     *@Description  
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplateTimeLine from "./example-page-template-time-line";

    export default {
        name: "example-page-template-time-line-example",
        components: {ExamplePageTemplateTimeLine, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '',
                key: 'prop'
              },
              {
                title: '',
                key: 'desc'
              },
              {
                title: '',
                key: 'type'
              },
              {
                title: '',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: ',',
                type: '*',
                default: ''
              },
              {
                prop: 'model',
                desc: 'fullModel,modelmodel,' +
                'fullModel,$rootfullModel,model,' +
                '"a.b.c",fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop: 'state',
                desc: '',
                type: 'String Number',
                default:''
              },
              {
                prop: 'qrCodeShow',
                desc: '',
                type: 'Boolean',
                default:'false'
              },
              {
                prop: 'pending',
                desc: '',
                type: 'Boolean',
                default:'false'
              },
              {
                prop: '',
                desc: '',
                type: '',
                default:''
              },
              {
                prop: '',
                desc: '',
                type: '',
                default:''
              }
            ]
          }
        }
    }
</script>



