<template>
  <ty-page-template :data="pageData"></ty-page-template>
</template>

<script>
  /**
   *@desc     
   *@author   
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-all",

    data() {
      return {
        pageData: {
          //
          page: {
            title: '',
            fullModel:{
              "templateCache": {
                "validators": [],
                "advList": [
                  {
                    "label": "()",
                    "value": 1,
                    "feeType": "()",
                    "feeExpression": "%s*%s*%s*%s|width,length,feeScale,amount",
                    "feeScale": 110,
                    "feeUnit": "/()"
                  },
                  {
                    "label": "",
                    "value": 2,
                    "feeType": "",
                    "feeExpression": "%s*%s|feeScale,amount",
                    "feeScale": 7000,
                    "feeUnit": "/"
                  }
                ],
                "tipAdvData": {
                  "amount": 0,
                  "width": 0,
                  "height": 0,
                  "feeExpression": "",
                  "feeType": "",
                  "feeScale": 0,
                  "feeUnit": "/"
                }
              }
            }
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'title',
                  data: {
                    title: ' type:title'
                  }
                },
                {
                  type: 'title',
                  data: {
                    title: ' type:step'
                  }
                },
                {
                  type: 'steps',
                  data:
                    {
                      current: 2,
                      steps:
                        [
                          {
                            title: '1'
                          },
                          {
                            title: '2'
                          },
                          {
                            title: '1'
                          },
                          {
                            title: '2'
                          }
                        ]
                    }
                },
                {
                  type: 'title',
                  data: {
                    title: ' type:form'
                  }
                },
                {
                  type: 'form',
                  data: {
                    //
                    model: {
                      normalLabel: '',
                      normalInput: '',
                      normalRadio: 'male',
                      normalDate:[],
                      startDate:'',
                      endDate:'',
                      normalSelect:''
                    },
                    //
                    rules: {
                      normalInput: [
                        {required: true, message: 'Please fill in the user name', trigger: 'blur'}
                      ],
                      // normalDate: [
                      //   {required: true, message: 'Please fill in the password.', trigger: 'blur'},
                      //   {
                      //     type: 'string',
                      //     min: 6,
                      //     message: 'The password length cannot be less than 6 bits',
                      //     trigger: 'blur'
                      //   }
                      // ]
                    },
                    //Form
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'label',
                          label: ' type:label',
                          prop: 'normalLabel',
                          //Form Item 
                          extend: {
                            labelWidth: 180
                          }
                        },
                        {
                          type: 'input',
                          label: ' type:input',
                          prop: 'normalInput',
                          extend:{
                            placeholder: '',
                            span:12
                          }
                        },
                        {
                          type: 'input',
                          label: ' type:input  item-type:password',
                          prop: 'pwdInput',
                          extend: {
                            type: 'password',
                            placeholder: '',
                          }
                        },
                        {
                          type: 'input',
                          label: ' type:input  item-type:textarea',
                          prop: 'mulInput',
                          extend: {
                            placeholder: '',
                            type: 'textarea',
                            rows: 2
                          }
                        },
                        {
                          type: 'radio',
                          label: ' type:radio',
                          prop: 'normalRadio',
                          extend:{
                          },
                          items: [
                            {
                              label: '',
                              value: 'male',
                              extend:{
                                size:'large',
                                disabled:true,
                              }
                            },
                            {
                              label: '',
                              value: 'female'
                            }
                          ],
                        },
                        {
                          type: 'date-picker',
                          label: ' type:date-picker',
                          prop: 'normaDate',
                          extend:{
                            placeholder:'',
                            type:'daterange',
                          },
                        },
                        {
                          type: 'date-picker-daterange',
                          label: ' type:date-picker-daterange',
                          extend:{
                          },
                          items:[
                            {
                              type:'date-picker',
                              prop:'startDate',
                              extend:{
                                placeholder:'1',
                                // width:150
                              },
                            },
                            {
                              type:'date-picker',
                              prop:'endDate',
                              extend:{
                                placeholder:'',
                              },
                            }
                          ],
                        },
                        {
                          type: 'select',
                          label: ' type:select',
                          prop: 'normalSelect',
                          extend:{
                            placeholder:'placeholder'
                          },
                          items: [
                            {
                              label: '',
                              value: 'male',
                              extend:{
                              }
                            },
                            {
                              label: '',
                              value: 'female'
                            }
                          ],
                        },
                      ]
                  }
                },
                {
                  type: 'title',
                  data: {
                    title: ' type:tip-adv'
                  }
                },
                {
                  "type": "tip-adv",
                  "data": {
                    "model": "templateCache.tipAdvData",
                    "eventType": "advTipUpdate",
                    "title": ""
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



