<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     
   *@author   
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-date-picker",
    data() {
      return {
        pageData: {
          //
          page: {
            title: ''
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //
                    model: {
                      normalLabel: '',
                      normalInput: '',
                      normalRadio: 'male',
                      normalDate:[],
                      startDate:'',
                      endDate:'',
                      normalSelect:''
                    },
                    //
                    rules: {
                      normalInput: [
                        {required: true, message: 'Please fill in the user name', trigger: 'blur'}
                      ],
                      // normalDate: [
                      //   {required: true, message: 'Please fill in the password.', trigger: 'blur'},
                      //   {
                      //     type: 'string',
                      //     min: 6,
                      //     message: 'The password length cannot be less than 6 bits',
                      //     trigger: 'blur'
                      //   }
                      // ]
                    },
                    //Form
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'date-picker',
                          label: ' type:date-picker',
                          prop: 'normaDate',
                          extend:{
                            placeholder:'',
                            type:'daterange',
                          },
                        },
                        {
                          type: 'date-picker-daterange',
                          label: ' type:date-picker-daterange',
                          extend:{
                          },
                          items:[
                            {
                              type:'date-picker',
                              prop:'startDate',
                              extend:{
                                placeholder:'1',
                                // width:150
                              },
                            },
                            {
                              type:'date-picker',
                              prop:'endDate',
                              extend:{
                                placeholder:'',
                              },
                            }
                          ],
                        }
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



