/**
 *@desc
 *@author  
 *@date    2019.9.3
 */
let Code = {};
Code.base = `<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  import TyDateTimePickerList from '../../../components/date-time-picker/date-time-picker-list'
  /**
   *@desc
   *@author  
   *@date    2018.12.6
   */
  export default {
    components: {
      TyDateTimePickerList
    },
    data() {
      return {
        pageData: {
          //
          page: {
            title: '',
            fullModel: {
              dataTimeRage: ['2018-12-11 00', '2018-12-20 10'],
              timeIntervals: [['', ''], ['', '']]
            }
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    rules: {
                      timeIntervals: [
                        {type: 'dateTimeRequired', message: '', trigger: 'blur'},
                        {type: "dateTimeDuration", message: '12', trigger: 'blur'},
                        {type: 'dateTimeOverlap', message: '', trigger: 'blur'},
                        {type: 'dataTimeExistInDataRange', message: '', dependField: 'dataTimeRage',trigger: 'blur'},
                        {type: 'dataDurationInNight', message: ',22:00-6:00', trigger: 'blur'}
                      ],
                      dataTimeRage: [
                        {type: 'dateRangeRequired', message: '', trigger: 'blur'},
                        {type: 'dataRangeIncludeDataTime', affectField: 'timeIntervals', trigger: 'blur'}
                      ]
                    },
                    //Form
                    extend: {
                      labelWidth:200,
                      span:12,
                    },
                    items:
                      [ {
                          type: 'dateTimeRange',
                          label: '',
                          prop: 'dataTimeRage',
                          extend: {
                            placeholder: '',
                            format: 'yyyy-MM-dd HH:00',
                            itemWidth: '',
                          }
                      }, {
                          type: 'dateTimeRangeList',
                          label: '',
                          prop: 'timeIntervals',
                          extend: {
                            maxNum: 2,
                            marginTop: '10px',
                            child: {
                              dependField: 'dataTimeRage',
                              itemWidth: '',
                              disabledDateOptions: 'outOfDateRange',
                              format: 'yyyy-MM-dd HH:00',
                              placeholder: ''
                            }
                          }
                        },
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    },
    methods: {
    }
  }
</script>
`;
export default Code;



