/**
 *@author       
 *@date         2018/9/28 02:20
 *@Copyright    
 *@Description  page-templatefullModel
 */
export const realModel={
  //note page-template
  inject:['template'],
  props: {
    //note ,$root,fullModel,,
    // secondfullModel.second
    model: {
      type: [String, Object],
      default: '$root',
    },
  },
  computed: {
    //
    fullModel(){
      return this.template.tplData.fullModel;
    },
    //note 
    realModel() {
      // console.log(this.template.tplData.fullModel)
      // console.log(this.model)

      if (typeof(this.model) === 'string') {
        if (this.model === '$root') {
          return this.template.tplData.fullModel;
        }
        let params = this.model.split('.');
        let result = this.template.tplData.fullModel;
        while (params.length > 0) {
          result = result[params.shift()];
        }
        return result;
      } else if (typeof(this.model) === 'object') {
        return this.model;
      }
      return {};
    }
  }
};



