<template>
    <examples-demo title=" radio" desc="" :code="code.formRadio"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-radio slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormRadio from "./example-page-template-form-radio";

  export default {
    name: "example-page-template-form-radio-demo",
    components: {ExamplePageTemplateFormRadio, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns:[
          {
            title: '',
            key: 'prop'
          },
          {
            title: '',
            key: 'desc'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'default'
          }
        ],
        data:[
          {
            prop: 'fullModel',
            desc: ',',
            type: '*',
            default: ''
          },
          {
            prop: 'model',
            desc: 'fullModel,modelmodel,' +
            'fullModel,$rootfullModel,model,' +
            '"a.b.c",fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop: 'extend:eventType',
            desc: 'eventTypecheckShowHide',
            type: 'String',
            default: ''
          },
          {
            prop: 'extend:showFlag',
            desc: '[{"key":"withBalloon","value": 1}],keyfullModelvaluevalue,',
            type: 'Array',
            default: ''
          },

        ]
      }
    }
  }
</script>



