<template>
  <div class="paymentPay" v-bind="$props" >
    <div class="chat clearfix">
      <div class="chat-img">
        <img src="../assets/img/icon-chat.png"/>
        <!--<img :src="chatImgUrl"/>-->
      </div>
      <ul class="chat-msg">
        <li class="msg-title">{{msgTitle}}</li>
        <li class="msg-content">{{msgContent}}{{applicationNumber}} {{receivables}}</li>
        <!--<li class="msg-alipay"><img :src="chatSmallImgUrl"/>  | 24</li>-->
        <li class="msg-alipay"><img src="../assets/img/icon-small-chat.png"/> {{paymentApp}} | 24</li>
      </ul>
      <div class="chat-money">
        <span class="alipay-money">{{alipayMoney}}</span>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "ty-payment-pay",
      props:{
        msgTitle:{
          type:String
        },
        msgContent:{
          type:String
        },
        applicationNumber:{
          type:String
        },
        receivables:{
          type:String
        },
        alipayMoney:{
          type:Number
        },
        chatImgUrl:{
          type:String
        },
        chatSmallImgUrl:{
          type:String
        },
        paymentApp:{
          type:String
        }

      },
      data(){
        return {

        }
      },
      methods:{},
      created(){}
    }
</script>



