<template>
      <GuideInfo v-bind="obj"></GuideInfo>
</template>
<script>
    import GuideInfo from "../../../components/guide-info";
    export default {
        name: "example-guide-info",
        components:{
          GuideInfo,
        },
        data(){
            return{
              obj:{
                  qrCode:'../assets/img/license.jpg',
                  title:'',
                  btnList:[
                    {label:'',type:'primary',value:1},
                    {label:'',type:'warning',value:2},
                    {label:'',type:'error',value:3},
                  ]
                }

            }
        }
    }
</script>



