<template>
    <examples-demo title=" alert" desc="" :code="code.tipAlert"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-tip-alert slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateTipAlert from "./example-page-template-tip-alert";

  export default {
    name: "example-page-template-tip-alert-demo",
    components: {ExamplePageTemplateTipAlert, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '',
            key: 'prop'
          },
          {
            title: '',
            key: 'desc'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'content',
            desc: ',v-html',
            type: 'String',
            default: ''
          }
        ]
      }
    }
  }
</script>



