                {title:'',num:'3',img:'../../img',mechanism:'',star:4.5,valid:false},
                {title:'',num:'3',img:'../../img',mechanism:'',star:3,valid:false},
                {title:'',num:'3',img:'../../img',mechanism:'',star:4,valid:true},
                {title:'',num:'2',img:'../../img',mechanism:'',star:3,valid:false},
                {title:'',num:'1',img:'../../img',mechanism:'',star:1.5,valid:true},
                {title:'',num:'4',img:'../../img',mechanism:'',star:2.5,valid:false},
                {title:'',num:'5',img:'../../img',mechanism:'',star:4,valid:true},
                {title:'',num:'3',img:'../../img',mechanism:'',star:3,valid:true}
              ]
            }

          }
        }
    }
</script>



