<template>
  <div class="calculator">
    <div class="title">2019()</div>
    <Form ref="form" :model="formItem" :label-width="180" :rules="ruleValidate">
      <FormItem label="">
        <Label>{{formItem.city}}</Label>
      </FormItem>
      <FormItem label="" prop="pay">
        <Input v-model="formItem.pay" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="socialDeduction">
        <Input v-model="formItem.socialDeduction" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="specialDeduction">
        <Input v-model="formItem.specialDeduction" placeholder=""></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="sumbit"></Button>
        <Button style="margin-left: 8px" @click="reset"></Button>
      </FormItem>
    </Form>
    <Table :columns="result.columns" :data="result.data"></Table>
    <Table :columns="base.columns" :data="base.data"></Table>
  </div>
</template>

<script>
  /**
   *@author       
   *@date         2019/2/2 09:49
   *@Copyright    
   *@Description
   *
   * 
   * =--
   * =--5000/---
   */
  export default {
    name: "calculator",
    props: {},
    data() {
      return {
        //
        formItem: {
          city: '',
          pay: '',                //
          socialDeduction: '',    //
          specialDeduction: '',   //
        },
        ruleValidate: {
          pay: [
            {required: true, message: '', trigger: 'blur'},
            {validator:this.validator, trigger: 'blur'}
          ],
          socialDeduction: [
            {required: true, message: '', trigger: 'blur'},
            {validator:this.validator, trigger: 'blur'}
          ],
          specialDeduction: [
            {required: true, message: '', trigger: 'blur'},
            {validator:this.validator, trigger: 'blur'}
          ]
        },
        formulae: {
          calculator: {
            month: 1,            //
            pay: 0,                             //
            socialDeduction: 0,     //
            specialDeduction: 0,   //
            rate: 0,             //
            quickDeduction: 0,   //
            accumulatedRate: '',   //,
            taxAmount: 0,                //
          },
          //
          taxCount: '(%s-%s-%s-5000)*%s|pay,socialDeduction,specialDeduction,month',
          //
          taxAmount: '(%s-%s-%s-5000)*%s*%s-%s%s|pay,socialDeduction,specialDeduction,month,rate,quickDeduction,accumulatedRate',
          //
          taxIncome: '%s-%s-%s|pay,socialDeduction,taxAmount'
        },
        //
        base: {
          //
          columns: [
            {
              title: '',
              key: 'level'
            },
            {
              title: '',
              key: 'annualTaxableIncom'
            },
            {
              title: '',
              key: 'rate',
              render: (h, {row}) => {
                return h('span', row.rate + "%");
              }
            },
            {
              title: '()',
              key: 'quickDeduction'
            }
          ],
          //
          data: [
            {
              level: 1,
              annualTaxableIncom: '36000',
              min: Number.MIN_SAFE_INTEGER,
              max: 36000,
              rate: 3,
              quickDeduction: 0
            },
            {
              level: 2,
              annualTaxableIncom: '36000-144000',
              min: 36001,
              max: 144000,
              rate: 10,
              quickDeduction: 2520
            },
            {
              level: 3,
              annualTaxableIncom: '144000-300000',
              min: 144001,
              max: 300000,
              rate: 20,
              quickDeduction: 16920
            },
            {
              level: 4,
              annualTaxableIncom: '300000-420000',
              min: 300001,
              max: 420000,
              rate: 25,
              quickDeduction: 31920
            },
            {
              level: 5,
              annualTaxableIncom: '420000-660000',
              min: 420001,
              max: 660000,
              rate: 30,
              quickDeduction: 52920
            },
            {
              level: 6,
              annualTaxableIncom: '660000-960000',
              min: 660001,
              max: 960000,
              rate: 35,
              quickDeduction: 85920
            },
            {
              level: 7,
              annualTaxableIncom: '960000',
              min: 960000,
              max: Number.MAX_SAFE_INTEGER,
              rate: 45,
              quickDeduction: 181920
            },
          ]
        },
        //
        result: {
          columns: [
            {
              title: '',
              key: 'month'
            },
            {
              title: '',
              key: 'taxIncomeAmount',
              render: (h, {row}) => {
                return h('span', accounting.formatMoney(row.taxIncomeAmount, "", 0))
              }
            },
            {
              title: '',
              key: 'rate',
              render: (h, {row}) => {
                return h('span', row.rate * 100 + "%");
              }
            },
            {
              title: '',
              key: 'taxAmountStr'
            },
            {
              title: '',
              key: 'taxAmount',
              render: (h, {row}) => {
                return h('span', accounting.formatMoney(row.taxAmount, "", 2))
              }
            },
            {
              title: '',
              key: 'taxIncome',
              render: (h, {row}) => {
                return h('span', accounting.formatMoney(row.taxIncome, "", 2))
              }
            },
          ],
          data: []
        }
      }
    },
    computed: {},
    methods: {
      validator(rule, value, callback) {
        if(rule.field==='pay'){
          if (value&&Number(value)>=5000) {
            callback();
          } else {
            callback(new Error(rule.message || '5000'));
          }
        }else{
          if (value&&Number(value)>=0) {
            callback();
          } else {
            callback(new Error(rule.message || '0'));
          }
        }

      },
      //note 
      calculatorCall(month) {
        if (month <= 12) {
          if (month === 1) {
            this.resetCaculator();
          }
          //
          const taxIncomeAmount = this.util.mathFormatStr(this.formulae.calculator, this.formulae.taxCount);
          //
          let levelData = this.getLevelData(taxIncomeAmount);
          if(!!levelData===false){
            this.util.alert({
              type:"error",
              content:'!'
            });
            return;
          }
          //
          this.formulae.calculator.quickDeduction = levelData.quickDeduction;
          //
          const rate = this.formulae.calculator.rate = levelData.rate / 100;
          //
          const taxAmountStr = this.util.getMathFormatStr(this.formulae.calculator, this.formulae.taxAmount)
          //
          const taxAmount = this.formulae.calculator.taxAmount = eval(taxAmountStr);
          //
          const taxIncome = this.util.mathFormatStr(this.formulae.calculator, this.formulae.taxIncome);
          //
          this.formulae.calculator.accumulatedRate += '-' + taxAmount
          //
          this.result.data.push(
            {
              month,
              taxIncomeAmount,
              rate,
              taxAmountStr,
              taxAmount,
              taxIncome
            }
          );
          this.formulae.calculator.month++
          this.calculatorCall(this.formulae.calculator.month);
        }
      },
      sumbit(){
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.calculatorCall(1)
          }
        })
      },
      //note 
      resetCaculator() {
        this.result.data = [];
        this.formulae.calculator = {
          month: 1,
          pay: this.formItem.pay,
          socialDeduction: this.formItem.socialDeduction,
          specialDeduction: this.formItem.specialDeduction,
          rate: 0,
          quickDeduction: 0,
          accumulatedRate: '-0',
          taxAmount: 0
        }
      },
      //note 
      getLevelData(money) {
        return _.find(this.base.data, function (o) {
          return money <= o.max && money >= o.min;
        });
      },
      //note 
      reset() {
        this.$refs.form.resetFields();
      }
    },
    created() {
      //
      document.title = "2019"
    },
  }
</script>

<style lang="less">
  .calculator {
    width: 1000px;
    margin: 30px auto;
    .title {
      text-align: center;
      width: 100%;
      font-weight: 400;
      font-size: 20px;
      margin-bottom: 10px;
    }
    form {
      width: 500px;
      border: #cccccc 1px solid;
      padding: 0 40px;
      margin: 10px auto;
    }
  }
</style>



