<template>
  <Button type="text" v-bind="$props" @click="toggle">{{label}}<Icon :type="iClass" /></Button>
</template>
<script>
  export default {
    name:'ty-button-switch',
    //el:,flag:
    props:{
      //
      flag:{
        default:false,
      },
      openLabel:{
        type:String,
        default:''
      },
      closeLabel:{
        type:String,
        default:''
      },
      //,flag
      change:{
        type:Function,
      }
    },
    data(){
      return {
      }
    },
    computed:{
      _flag:{
        get(){
          return this.flag;
        },
        set(val){
          this.$emit('update:flag',val);
          this.change&&this.change(val)
        }
      },
      //
      label(){
        if(this._flag){
          return this.closeLabel
        }else{
          return this.openLabel
        }
      },
      //class
      iClass(){
        if(this._flag){
          return 'ios-arrow-up'
        }else{
          return 'ios-arrow-down'
        }
      }
    },
    methods:{
      //
      toggle(){
        this._flag=!this._flag;
      }
    }
  }
</script>



