<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
  /**
   *@desc     
   *@author   
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-label",
    data() {
      return {
        pageData: {
          //
          page: {
            title: '',
            fullModel:{
              normalLabel: '1',
              applyName:'',
              customerFullName:'',
              applyName1:'1'
            }
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //
                    // model: {
                    //   normalLabel: '',
                    // },
                    //Form
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'label',
                          label: ' type:label',
                          prop: 'normalLabel',
                          //Form Item 
                          extend: {
                            labelWidth: 180
                          }
                        },
                        {
                          type: "label",
                          label: "",
                          prop: "applyName",
                          extend: {
                            linkageChangeValue: [
                              {
                                type: "out",
                                changeKey: "customerFullName",
                                noChangeExist: true,
                                values: [
                                  {
                                    changeParam: "applyName"
                                  }
                                ]
                              }
                            ]
                          }
                        },
                        {
                          type: "input",
                          label: "",
                          prop: "customerFullName",
                          extend: {
                            placeholder: "",
                            itemWidth: 322
                          }
                        }
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



