<template>
  <div>
    <TableFixed v-bind="fixedData" style="width: 1200px"></TableFixed>
  </div>
</template>
<script>
    /**
     *@author       
     *@date         2018/9/17 14:38
     *@Copyright    
     *@Description
     */
    import TableFixed from '../../../components/table-fixed'
    export default {
      name: "example-table-fixed",
      components: {TableFixed},
      data(){
        return {
          fixedData: {
            columns: [
              {
                title: '',
                key: 'materialName',
                align:'center',
                fixed: 'left',
                width:200,
                render: (h, params) => {
                  if(params.row.type=='fee'){
                    return h('div', [
                      h('P',params.row.materialName),
                      // h('TyContentElementLookupFee')
                    ]);
                  }else if(params.row.type=='downLoad'){
                    return h('div', [
                      h('P',params.row.materialName),
                      h('Tag', {
                        props: {
                          color:'primary',
                        }
                      }, ''),
                    ]);
                  }

                }
              },
              {
                title: '',
                key: 'materialType',
                align:'center',
                width:200
              },
              {
                title: '',
                key: 'sourceChannel',
                align:'center',
                width:200,
                render:(h,params)=>{
                  return h('div', [
                    h('P',params.row.sourceChannel),
                    h('TyContentElementLookupMap')
                  ]);
               }
              },
              {
                title: '',
                key: 'materialPaper',
                align:'center',
                width:200
              },
              {
                title: '',
                key: 'notification',
                align:'center',
                width:200
              },
              {
                title: '',
                key: 'admissibilityStandard',
                align:'center',
                width:200
              },
              {
                title: '',
                key: 'isEleMaterials',
                align:'center',
                width:200
              },
              {
                title: '',
                key: 'remark',
                align:'center',
                width:200
              },
            ],
            data: [
              {
                materialName: '',
                materialType: 'bmpgifpngjpgjpeg',
                sourceChannel: '',
                materialPaper: '1A4',
                notification: '',
                admissibilityStandard:'',
                isEleMaterials:'',
                remark:'',
                type:'downLoad'
              },
              {
                materialName: '',
                materialType: 'bmpgifpngjpgjpeg',
                sourceChannel: '',
                materialPaper: '1A4',
                notification: '',
                admissibilityStandard:'',
                isEleMaterials:'',
                remark:'',
                type:'fee'
              },
            ]
          },
        }
      }
    }
</script>

<style scoped>

</style>



