<template>
    <examples-demo title=" select" desc="" :code="code.formSelect"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-select slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormSelect from "./example-page-template-form-select";

  export default {
    name: "example-page-template-form-radio-demo",
    components: {ExamplePageTemplateFormSelect, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns:[
          {
            title: '',
            key: 'prop'
          },
          {
            title: '',
            key: 'desc'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'default'
          }
        ],
        data:[
          {
          prop: 'fullModel',
          desc: ',',
          type: '*',
          default: ''
        },
          {
            prop: 'model',
            desc: 'fullModel,modelmodel,' +
            'fullModel,$rootfullModel,model,' +
            '"a.b.c",fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop:'rules',
            desc:' async-validator',
            type:'Object|Array',
            default:''
          },
          {
            prop:'extend',
            desc:'form',
            type:'Object',
            default:''
          },
          {
            prop:'labelWidth',
            desc:'form-itemlabel',
            type:'Number',
            default:'205'
          },
          {
            prop:'span',
            desc:'form-item 24',
            type:'Number',
            default:'24'
          },
          {
            prop:'items',
            desc:'form-item',
            type:'Array',
            default:''
          },
          {
            prop:'extend',
            desc:'form-itemplaceholder,span',
            type:'Object',
            default:''
          },
          {
            prop:'items',
            desc:'form-item',
            type:'Array',
            default:''
          },
          {
            prop:'excat',
            desc:'typelookup-maplookup-feelookup-projectlookup-numberlookup-querytip-advform-item',
            type:'Array',
            default:''
          },
          {
            prop:'dynamic',
            desc:',urlparams,label:"",value:"" labelvalue',
            type:'Object',
            default:''
          }
        ]
      }
    }
  }
</script>



