import axios from 'axios';
import {Spin} from 'iview';
import qs from 'qs'
import util from './util'
import config from './config'

axios.defaults.timeout = 1000 * 15;
axios.defaults.withCredentials = true
//
axios.interceptors.request.use(
  function (config) {
    //
    if (config.headers['Show-Loading'] === false) {

    } else {
      util.showLoading();
    }
    // console.log(':' + config.url)
    if (config.method == "post") {
      config.data = qs.stringify(config.data);
      config.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';
      config.headers['X-Requested-With'] = 'XMLHttpRequest';
    }
    return config;
  },
  function (error) {
    return Promise.reject(error);
  });
//
axios.interceptors.response.use(
  function (res) {
    util.closeLoading();
    let result;
    console.log(":", res.data);
    if (res.data.code == 0) {
      if (typeof (res.data.data) === "undefined") {
        result = res.data;
      } else {
        result = res.data.data;
      }
      //,data,data
      // const values = Object.values(result);
      // if (values.length === 1 && (values[0] instanceof Array)) {
      //   result = values[0];
      // }
      return result;
    }
    //code 0
    let code, message;
    if (res.config.mock) {
      code = 10000;
      message = res.data.errMsg
    } else {
      code = res.data.code;
      message = res.data.message;
    }
    errorHandler(message);
    return Promise.reject({code, message});
  },
  function (error) {
    util.closeLoading();
    console.log(":",error);
    if (error.code) {
      // errorHandler(error.message);

      if (error.code == 'ECONNABORTED' && error.config.url.includes('ocr/ocrAbility') ) {
        console.log(', ');
        return Promise.reject(error)
      }
      errorHandler();
      return Promise.reject(error);
    }
    if (error.response) {
      switch (error.response.status) {
        case 302:
          if (config.env == 'local:test' || config.env == 'local') {
            return;
          }
          util.alert({
            type: 'error',
            content: ', '
          });
          util.login();
          break;
        //
        case 401:
          if (config.env == 'local:test' || config.env == 'local') {
            return;
          }
          sessionStorage.clear();
          util.login();
          break;
        //
        case 403:
          errorHandler('!')
          break;
        default:
          // errorHandler(error.message)
          break;
      }
    }
    return Promise.reject(error);
  });

function errorHandler(message, title = '') {
  util.alert({
    type: 'error',
    content: message || ',!'
  });
}

export default axios



