<template>
  <div class="ty-content-element-table-retire">
    <ty-content-element-tip-alert class="ty-content-element-table-retire" :closable="false"
                                  :content="`:${transfering}`"/>
    <Input class="input-search" v-model="formData.key" search enter-button placeholder="">
    <div slot="append">
      <Button icon="ios-search" @click="search"/>
    </div>
    </Input>
    <Button class="btn-append" @click="advance">{{btnAdvanceLabel}}</Button>
    <Form :labelWidth="130" v-if="isAdvance">
      <FormItem label=":">
        <RadioGroup v-model="formData.isRetire" type="button">
          <Radio size="small" label="1"></Radio>
          <Radio label="0"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label=":">
        <RadioGroup v-model="formData.sex" type="button">
          <Radio label="male"></Radio>
          <Radio label="female"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label=":">
        <RadioGroup v-model="formData.state" type="button">
          <Radio label="1"></Radio>
          <Radio label="2"></Radio>
          <Radio label="3"></Radio>
          <Radio label="4"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem>
        <Button @click="advance"></Button>
      </FormItem>
    </Form>
    <div style="min-height: 100px">
      <IvTable ref="table" v-bind="table" @onRowClick="onRowClick"/>
    </div>
  </div>
</template>
<script>
  import IvTable from "../../../iv-table";
  import TyContentElementTipAlert from "../content-element-tip/content-element-tip-alert";

  /**
   *@author       
   *@date         2018/9/24 01:00
   *@Copyright    
   *@Description  
   */
  export default {
    name: "ty-content-element-table-retire",
    components: {
      TyContentElementTipAlert,
      IvTable
    },
    props: {
      eventType: {
        type: String
      }
    },
    data() {
      return {
        btnAdvanceLabel: '',
        isAdvance: false,
        //
        transfering: '',
        formData: {
          key: '',
          isRetire: '',
          sex: '',
          state: ''
        },
        table: {
          url: 'test',
          // pageNo: 1,
          singleSelect: false,
          data: [{
            name: '',
            phone: '601100195205081332',
            address: '1115-102'
          }, {}],
          height: 300,
          queryParam: function (param) {
            console.log('queryParam:', param)
            return param
          },
          responseHandler: function (val) {
            console.log('responseHandler:', val)
            return val;
          },
          columns: [{
            width: 60,
            type: 'selection',
            align: 'center',
          },
            {
              title: '',
              key: 'inquiryCode',
              align: 'center',
              render(h, {row}) {
                return h('ty-retire-info',
                  {
                    props: {
                      data: row
                    }
                  });
              }
            },
            {
              title: '',
              key: 'professionalOrgName',
              align: 'center',
              width: 120
            },
            {
              title: '',
              key: 'professionalOrgName',
              align: 'center',
              width: 120,
              render(h, {row}) {
                return h('ty-retire-require-state',
                  {
                    props: {
                      // data:row
                    }
                  });
              }
            },
            {
              title: '',
              key: 'professionalOrgName',
              align: 'center',
              width: 120,
              render:(h, {row})=>{
                return h('div', [
                  h('Button', {
                      props: {
                        type: 'success'
                      },
                      on: {
                        click: () => {
                          this.applicationForm(row.id)
                        }
                      }
                    },
                    ''
                  ),
                  h('a', {
                      on: {
                        click: () => {
                          this.lookDetail(row.id)
                        }
                      },
                    },
                    ''
                  )
                ])
              }
            },
          ]
        }
      }
    },
    methods: {
      //note 
      onRowClick(row, index) {
        this.selectIndex = index;
      },
      //note 
      advance() {
        this.isAdvance = !this.isAdvance;
        if (this.isAdvance) {
          this.btnAdvanceLabel = ''
        } else {
          Object.assign(this.formData,
            {
              isRetire: '',
              sex: '',
              state: ''
            });
          this.btnAdvanceLabel = ''
        }
      },
      //note 
      search() {
        this.$refs.table.query(this.formData);
      },
      //note 
      lookDetail(id) {
        console.log('lookDetail')
      },
      //note 
      applicationForm(id) {

      }
    }
  }
</script>



