<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
    /**
     *@author       
     *@date         2018/9/10 14:21
     *@Copyright    
     *@Description
     */
    export default {
        name: "example-page-template-info-attachment",
        data() {
          return {
            pageData: {
              //
              page: {
                fullModel: {
                  infoAttachment:[
                    {
                      attrName:'attrName',
                      name: "",
                      fileUrl: "1"
                    },
                    {
                      attrName:'attrName',
                      name: "",
                      fileUrl: "2"
                    },
                    {
                      attrName:'attrName',
                      name: "",
                      fileUrl: "3"
                    },
                    {
                      attrName:'attrName',
                      name: "",
                      fileUrl: "4"
                    },
                    {
                      attrName:'attrName',
                      name: "",
                      fileUrl: "5"
                    }
                  ],
                }
              },
              //
              elements: [
                {
                  type: "box",
                  data: [
                    {
                      type: "info-attachment",
                      data: {
                        model:"infoAttachment",
                        headerTitle: "",
                        showHeader: true
                      }
                    }
                  ]
                }
              ]
            }
          }
        },
        methods:{

        }
    }
</script>



