let Code = {};
Code.base=`
<template>
  <div>
    <RebirthRadio :active="rebirthStep" :radioData="rebirthStepData"></RebirthRadio>
  </div>
</template>

<script>
  import RebirthRadio from '../../../components/special-radio'
  export default {
    name: "user-info",
    components: {RebirthRadio},
    data(){
      return {
        rebirthStep:0,
        rebirthStepData: [
          {content:''},
          {content:''},
          {content:''},
          {content:''}
        ]
      }
    }
  }
</script>
`;
export default Code;



