<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     
   *@author   
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-tip-alert",
    data() {
      return {
        pageData: {
          //
          page: {
            title: ''
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'tip-alert',
                  data:
                    {
                      content:'<a></a>',
                    }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



