<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     
   *@author   
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-input",
    data() {
      return {
        pageData: {
          //
          page: {
            title: ''
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //
                    model: {
                      normalInput: '',
                    },
                    //
                    rules: {
                      normalInput: [
                        {required: true, message: 'Please fill in the user name', trigger: 'blur'}
                      ],
                    },
                    //Form
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'input',
                          label: ' type:input',
                          prop: 'normalInput',
                          extend:{
                            placeholder: '',
                            span:12
                          }
                        },
                        {
                          type: 'input',
                          label: ' type:input  item-type:password',
                          prop: 'pwdInput',
                          extend: {
                            type: 'password',
                            placeholder: '',
                          }
                        },
                        {
                          type: 'input',
                          label: ' type:input  item-type:textarea',
                          prop: 'mulInput',
                          extend: {
                            placeholder: '',
                            type: 'textarea',
                            rows: 2
                          }
                        },
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



