<template>
  <div class="textTpl">
    <p>{{content}}</p>
  </div>

</template>

<script>
  /**
   *@author       
   *@date         2018/10/13 16:20
   *@Copyright    
   *@Description
   */
  import {realModel} from "../../content-box/mixin-page-template";
  export default {
    mixins:[realModel],
    name: "content-element-text-tpl",
    props: {
      contentType: {
        type:String
      }
    },
    data() {
      return {
        content:''
      }
    },
    methods:{
      onChange(){
        if (this.contentType == 'commitment') {
          this.content = this.realModel.applyName + this.realModel.legalMan + ''+ this.realModel.applyName + this.realModel.legalMan+''
          this.realModel.commitment = this.content
        }
      },
      replaceTxt(value){
        const txt = value||this.realModel.templateCache.commitment;
      }
    },
    created() {
      if (this.contentType == 'commitment') {
        this.content = this.realModel.applyName + this.realModel.legalMan + ''+ this.realModel.applyName + this.realModel.legalMan+''
        this.realModel.commitment = this.content
      }
      if (this.contentType == 'commitmentLetter') {
          let date = new Date()
          let year = date.getFullYear();
          let month = date.getMonth() + 1;
          let day = date.getDate();
          this.content = '' + year + '' + month + '' + day + ''
          this.realModel.commitmentLetter = this.content
      }
      this.replaceTxt();
      Bus.$on('changeLegal',this.onChange);
    }
  }
</script>

<style lang="less">
  .textTpl {
    textarea {
      height: 100%;
      width: 100%;
      padding-left: 8px;
    }
  }
</style>




