<template>
  <div class="ty-content-element-lookup ty-content-element-lookup-map">
    <a class="search-map" @click="open"></a>
    <Modal class="ty-content-element-lookup-map" :scrollable="true"
           v-model="modal" width="750">
      <div slot="header">
        <span></span>
      </div>
      <div id="outer-box">
        <iframe id="mapPage" width="100%" height="100%" frameborder=0
                src="https://apis.map.qq.com/tools/locpicker?search=1&type=1&key=WGOBZ-66WH6-JMLSV-M3PTI-KJTHK-PLF24&referer=myapp">
        </iframe>
      </div>
      <div slot="footer">
        <Button type="primary" @click="ok"></Button>
        <Button @click="cancel"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import IvTable from "../../../iv-table";

  /**
   *@author       
   *@date         2018/9/7 01:00
   *@Copyright    
   *@Description  
   */
  export default {
    name: "ty-content-element-lookup-map",
    components: {IvTable},
    data() {
      return {
        modal: false,
        place: '',
      }
    },
    methods: {
      //note 
      mapHandler(event) {
        // 
        const loc = event.data;
        if (loc && loc.module === 'locationPicker') {
          //postmodule'locationPicker'
          this.place=loc.poiname+loc.poiaddress;
        }
      },
      //note 
      open() {
        this.modal = true;
        window.removeEventListener('message',this.mapHandler , false);
        window.addEventListener('message',this.mapHandler , false);
      },
      //note 
      ok() {
        if (this.eventType) {
          Bus.$emit(this.eventType, this.place)
        }
        this.$emit('change', this.place)
        this.modal = false;
      },
      //note 
      cancel() {
        this.modal = false;
      },
    }
  }
</script>
<style>
  #outer-box {
    height: 400px;
  }

  .map {
    float: left;
    height: 100%;
    width: 450px;
  }

  #panel {
    /*position: absolute;*/
    top: 0;
    bottom: 0;
    right: 0;
    height: 100%;
    overflow: auto;
    width: 300px;
    z-index: 999;
    border-left: 1px solid #eaeaea;
    background: #fff;
  }

  #searchBar {
    height: 30px;
    background: #ccc;
  }

  #searchInput {
    width: 100%;
    height: 30px;
    line-height: 30%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border: none;
    border-bottom: 1px solid #ccc;
    padding: 0 5px;
  }

  #searchResults {
    overflow: auto;
    height: calc(100% - 30px);
  }

  .amap_lib_placeSearch,
  .amap-ui-poi-picker-sugg-container {
    border: none !important;
  }

  .amap_lib_placeSearch .poibox.highlight {
    background-color: #CAE1FF;
  }

  .poi-more {
    display: none !important;
  }
</style>



