<template>
  <Row type="flex" :justify="extend.justify||'center'">
    <Col :span="extend.span||12">
    <Form class="ty-content-element-form" :label-width="extend.labelWidth||205" v-bind="extend">
      <FormItem style="position: relative">
        <span style="display: inline-block;position: absolute;left: -30px"><Checkbox
          v-model="realModel['isAgree']"></Checkbox></span> {{content}}
      </FormItem>
    </Form>
    </Col>
  </Row>
</template>

<script>
  /**
   *@author       
   *@date         2018/1/9 16:57
   *@Copyright    
   *@Description
   */
  import {realModel} from "./content-box/mixin-page-template";

  export default {
    name: "agreeCommitment",
    mixins: [realModel],
    props: {
      top: {
        type: Number,
        default: 0
      },
      book: {
        type: Array
      },
      bookContent: {
        type: String
      },
      extend: {
        default() {
          return {};
        }
      },
      text: {
        type: String
      },
      type: {
        type: String,
        default: "1"
      },
      prop: {
        type: String,
        default: "isAgree"
      }
    },
    watch: {},
    data() {
      return {
        //
        isAgree: false,
        bookArr: this.book,
        content: "",
        modal1: false,
        //
        modalContent: '',
        modalTitle: '',
        //checkbox
        dis: true,
        //60s
        canCancel: false,
        second: 0,
        waitTime: 0,
        num: [],
        index: null
      }
    },
    methods: {},
    created() {
      console.log(this.prop)
      if (this.prop == 'commitmentLetter') {
        let date = new Date()
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();
        this.content = '' + year + '' + month + '' + day + ''
        this.realModel.commitmentLetter = this.content
      }else{
        this.content = this.bookContent
      }
    }
  }
</script>

<style scoped>


</style>



