<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     
   *@author   
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-lookup-map",
    data() {
      return {
        pageData: {
          //
          page: {
            title: ''
          },
          //
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'lookup-map',
                  data: {
                    title: ' type:lookup-map'
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



