<template>
  <examples-demo title=" info-base" desc="" :code="code.basicInfo"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-basic-info slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       
     *@date         2018/9/10 11:57
     *@Copyright    
     *@Description  
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplateBasicInfo from "./example-page-template-basic-info";
    export default {
        name: "example-page-template-basic-info-demo",
        components: {ExamplePageTemplateBasicInfo, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '',
                key: 'prop'
              },
              {
                title: '',
                key: 'desc'
              },
              {
                title: '',
                key: 'type'
              },
              {
                title: '',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: ',',
                type: '*',
                default: ''
              },
              {
                prop: 'model',
                desc: 'fullModel,modelmodel,' +
                'fullModel,$rootfullModel,model,' +
                '"a.b.c",fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop:'arrow',
                desc:'',
                type:'Boolean',
                default:'true'
              },
              {
                prop:'footer',
                desc:'footer',
                type:'Boolean',
                default:'false'
              },
              {
                prop:'basicInfo:cells',
                desc:'',
                type:'Array',
                default:'[]'
              },
              {
                prop:'cells:col',
                desc:'24/col',
                type:'Number',
                default:''
              },
              {
                prop:'cells:headerTitle',
                desc:'header',
                type:'String',
                default:''
              },
              {
                prop:'cells:showHeader',
                desc:'header',
                type:'Boolean',
                default:''
              },
              {
                prop:'cells:showFooter',
                desc:'footer',
                type:'Boolean',
                default:''
              },
              {
                prop:'cells:data',
                desc:'',
                type:'Array',
                default:''
              },
              {
                prop:'cells:same',
                desc:' 212-3 ',
                type:'*',
                default:''
              },
              {
                prop:'data:title',
                desc:'',
                type:'String',
                default:''
              },
              {
                prop:'data:prop',
                desc:'fullModelprop',
                type:'*',
                default:''
              },
              {
                prop:'data:label',
                desc:' ReformisOutdatesingleselecttable',
                type:'String',
                default:''
              },
              {
                prop:'data:itemProp',
                desc:'label=select',
                type:'String',
                default:''
              },
              {
                prop:'data:unit',
                desc:'',
                type:'String',
                default:''
              }
            ]
          }
        }
    }
</script>

<style scoped>

</style>



